.snackBar{
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #7ef026;
  box-shadow: 0px -4px 7px rgba(0, 0, 0, 0.1), 0px 4px 7px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  position: fixed;
  bottom: 5px;
  right: 5px;
  padding: 10px;
  overflow: hidden;
  &__icon{
    width: 20px;
    height: 20px;
  }
  &__text{
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    color: #2f3030;
    letter-spacing: 2px;
    &_paddingLeft{
      padding-left: 10px;
    }
  }
}